import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import img from "../../assets/logo/Delivery/Png/Ver 1/Logo White-02.png"
import Category from '../section-components/category';
import './navbar-v5.css'


class NavbarV5 extends Component {
	componentDidMount() {

		const $ = window.$;

		let publicUrl = process.env.PUBLIC_URL + '/'

		var shoppingCart = (function () {
			// =============================
			// Private methods and propeties
			// =============================
			var cart = [];
			var item;
			var itemCopy;
			var p;

			// Constructor
			function Item(name, price, count, img) {
				this.name = name;
				this.price = price;
				this.count = count;
				this.img = img;
			}

			// Save cart
			function saveCart() {
				sessionStorage.setItem('shoppingCart', JSON.stringify(cart));
			}

			// Load cart
			function loadCart() {
				cart = JSON.parse(sessionStorage.getItem('shoppingCart'));
			}
			if (sessionStorage.getItem("shoppingCart") != null) {
				loadCart();
			}


			// =============================
			// Public methods and propeties
			// =============================
			var obj = {};

			// Add to cart
			obj.addItemToCart = function (name, price, count, img) {
				for (var item in cart) {
					if (cart[item].name === name) {
						cart[item].count++;
						saveCart();
						return;
					}
				}
				var item = new Item(name, price, count, img);
				cart.push(item);
				saveCart();
			}
			// Set count from item
			obj.setCountForItem = function (name, count) {
				for (var i in cart) {
					if (cart[i].name === name) {
						cart[i].count = count;
						break;
					}
				}
			};
			// Remove item from cart
			obj.removeItemFromCart = function (name) {
				for (var item in cart) {
					if (cart[item].name === name) {
						cart[item].count--;
						if (cart[item].count === 0) {
							cart.splice(item, 1);
						}
						break;
					}
				}
				saveCart();
			}

			// Remove all items from cart
			obj.removeItemFromCartAll = function (name) {
				for (var item in cart) {
					if (cart[item].name === name) {
						cart.splice(item, 1);
						break;
					}
				}
				saveCart();
			}

			// Clear cart
			obj.clearCart = function () {
				cart = [];
				saveCart();
			}

			// Count cart 
			obj.totalCount = function () {
				var totalCount = 0;
				for (var item in cart) {
					totalCount += cart[item].count;
				}
				return totalCount;
			}

			// Total cart
			obj.totalCart = function () {
				var totalCart = 0;
				for (var item in cart) {
					totalCart += cart[item].price * cart[item].count;
				}
				return Number(totalCart.toFixed(2));
			}

			// List cart
			obj.listCart = function () {
				var cartCopy = [];
				var i = '0';
				for (i in cart) {
					item = cart[i];
					itemCopy = {};
					for (p in item) {
						itemCopy[p] = item[p];

					}
					itemCopy.total = Number(item.price * item.count).toFixed(2);
					cartCopy.push(itemCopy)
				}
				return cartCopy;
			}

			return obj;
		})();


		// *****************************************
		// Triggers / Events
		// ***************************************** 
		// Add item
		$(document).on("click", ".add-to-cart", function (event) {
			event.preventDefault();
			var name = $(this).data('name');
			var img = $(this).data('img');
			var price = Number($(this).data('price'));
			shoppingCart.addItemToCart(name, price, 1, img);
			displayCart();
			$(this).prepend('<div class="succes-message">Item Added</div>');
			$('.succes-message').show(0).delay(500).hide(0);
			setTimeout(function () {
				$(".succes-message").remove();
			}, 600);
		});


		// Clear items
		$(document).on("click", ".clear-cart", function (event) {
			shoppingCart.clearCart();
			displayCart();
		});


		function displayCart() {

			var cartArray = shoppingCart.listCart();
			var output = "";
			for (var i in cartArray) {
				output += '<li>'
					+ '<div class="d-flex">'
					+ '<div class="thumb">'
					+ '<img src="' + publicUrl + cartArray[i].img + '" alt="">'
					+ '</div>'
					+ '<div class="content">'
					+ '<h6 class="title"><a href="single-product.html">' + cartArray[i].name + '</a></h6>'
					+ '<span class="price">$' + cartArray[i].price + '</span>'
					+ '</div>'
					+ ' <div class="action">'
					+ '<input type="number" disabled data-name="' + cartArray[i].name + '" value="' + cartArray[i].count + '">'
					+ '<a href="#" class="remove delete-item" data-name="' + cartArray[i].name + '">Remove</a>'
					+ ' </div>'
					+ '</div>'
					+ '</li>';
			}


			$('.show-cart').html(output);
			$('.total-cart').html(shoppingCart.totalCart());
			$('.total-count').html(shoppingCart.totalCount());
		}

		// Delete item button

		$('.show-cart').on("click", ".delete-item", function (event) {
			var name = $(this).data('name')
			shoppingCart.removeItemFromCartAll(name);
			displayCart();
		})




		// Item count input
		$('.show-cart').on("change", ".item-count", function (event) {
			var name = $(this).data('name');
			var count = Number($(this).val());
			shoppingCart.setCountForItem(name, count);
			displayCart();
		});

		displayCart();
	}
	render() {
		let publicUrl = process.env.PUBLIC_URL + '/'
		let imgattr = 'logo'
		let anchor = '#'
		return (
			<div className="stoon-navbar absolute-nav">
				<div className="header-top dark-header-top d-none d-sm-block">
					<div className="container-fluid">
						<div className="row align-items-center">
							<div className="col-lg-6 col-6">
								<div className="contact">
									<a href="tel:+496170961709"><i className="icon-call-header" /> (939) 353-1107</a>
									<a href="#"><i className="icon-email-subscribe" /> info@redzari.in</a>
								</div>
							</div>
							<div className="col-lg-6 col-6">
								<div className="right-nav text-right">
									<ul>
										<li>
											<div className="select-menu">
												<select className="menu-select">
													<option>English</option>
												</select>
											</div>
										</li>
										<li>
											<div className="select-menu">
												<select className="menu-select">
													<option>IND</option>
													<option>USD</option>
												</select>
												<i className="fa fa-chevron-down" />
											</div>
										</li>
										<li>
											<a href="#"><i className="icon-user" /></a>
											<ul className="user-dropdown">
												<Link to="/login" ><li><a href="#">Sign In | Join</a></li></Link>
												<Link to="/account" ><li><a href="#"><i className="icon-user" /> My Account</a></li></Link>
												<li><a href="#"><i className="fa fa-briefcase" /> My Orders</a></li>
												<li><a href="#"><i className="fa fa-reply" /> Returns Information</a></li>
												<li><a href="#"><i className="fa fa-commenting-o" /> Contact Preferences</a></li>
											</ul>
										</li>
									</ul>
								</div>
							</div>
						</div>
					</div>
				</div>
				<nav className="navbar navbar-area navbar-expand-lg nav-style-01">
					<div className="container-fluid nav-container">
						<div className="row">
							<div className="col-lg-3 col-4 order-1 align-self-center">
								<div className="logo">
									<Link to="/"><img src={img} width='80px' height='80px' alt="hello " /></Link>
								</div>
							</div>
							<div className="col-lg-6 order-3 order-lg-2">
								<div className="collapse navbar-collapse" id="shop-menu">
									<ul className="navbar-nav menu-open">
										<li>
											<a href="#">Home</a>
										</li>
										<li className='shop-btn'>
											<Link>
												<a href="#"  >Shop <i className="fa fa-angle-down" />
													<div>
														<div className='shop-drop' ><Category /></div>
													</div>
												</a>
											</Link>
										</li>
										{/* <li className="menu-item-has-children">
											<a href="#">Pages <i className="fa fa-angle-down" /></a>
											<ul className="sub-menu">
												<li><Link to="/blog">Blog</Link></li>
												<li><Link to="/blog-details">Blog Details</Link></li>
												<li><Link to="/product-details">Single Product</Link></li>
												<li><Link to="/shoping-cart">Shoping Cart</Link></li>
												<li><Link to="/faq">FAQ</Link></li>
												<li><Link to="/error">Error</Link></li>
												<li><Link to="/comming-soon">Coming Soon</Link></li>
											</ul>
										</li> */}
										<li><Link to="/about">About Us</Link></li>
										<li><Link to="/contact">Contact us</Link></li>
									</ul>
								</div>
							</div>
							<div className="col-lg-3 col-8 justify-content-end d-flex order-2 order-lg-3">
								<div className="nav-right-part nav-right-part-02">
									<ul>
										<li>
											<a href="#" id="search"><i className="icon-search" /></a>
										</li>
										<li>
											<a href="#"><i className="icon-heart" /><span className="badge badge-pink">2</span></a>
										</li>
										<li className="has-dropdown">
											<a href="#"><i className="icon-add-to-cat" /><span className="badge  total-count"></span></a>
											<ul>
												<span className="show-cart">
												</span>
												<li><h6><span>Total price: $</span><span className="total-cart"></span></h6><div className="btn-wrapper"> <Link to="/shoping-cart" className="btn btn-cart">Go to cart</Link></div></li>
											</ul>
										</li>
									</ul>
								</div>
								<div className="responsive-mobile-menu">
									<div className="menu toggle-btn d-block d-lg-none" data-toggle="collapse" data-target="#shop-menu" aria-expanded="false" role="button">
										<div className="icon-left" />
										<div className="icon-right" />
									</div>
								</div>
							</div>
						</div>
					</div>
				</nav>
			</div>

		)
	}
}


export default NavbarV5